import AnswersState from "./state";
import AnswersAction, { GET_ANSWERS_START, GET_ANSWERS_SUCCESS, GET_ANSWERS_ERROR, ADD_ANSWER_SUCCESS, ADD_ANSWER_ERROR, EDIT_ANSWER_SUCCESS, EDIT_ANSWER_ERROR } from "./actions";
import { RootState } from "..";
import { Answer } from "./answer";

const defaultState : AnswersState = {};

export const selectAnswers = (state : RootState) => state.answers;

export default function AnswersReducer(state : AnswersState = defaultState, action : AnswersAction) : AnswersState {
    switch (action.type) {
        case GET_ANSWERS_START:
            state[action.weekId] = { status: 'loading', answers: {} };
            return {...state};
        case GET_ANSWERS_ERROR:
            state[action.weekId] = { status: 'errored', answers: {} };
            return {...state};
        case GET_ANSWERS_SUCCESS:
            const answerDictionary : { [personId : number] : Answer } = {};
            action.data.forEach(answer => answerDictionary[answer.personId] = answer);
            state[action.weekId] = { status: 'loaded', answers: answerDictionary };
            return {...state};
        case ADD_ANSWER_SUCCESS:
            state[action.weekId] = { status: 'unloaded', answers: {} };
            return {...state};
        case ADD_ANSWER_ERROR:
            state[action.weekId] = { status: 'unloaded', answers: {} };
            return {...state};
        case EDIT_ANSWER_SUCCESS:
            state[action.weekId] = { status: 'unloaded', answers: {} };
            return {...state};
        case EDIT_ANSWER_ERROR:
            state[action.weekId] = { status: 'unloaded', answers: {} };
            return {...state};
        default:
            return state;
    }
}