import api from '../api';
import { Dispatch } from 'redux';
import { EDIT_ANSWER_SUCCESS, EDIT_ANSWER_ERROR, EDIT_ANSWER_START } from '../../reducers/answers/actions';
import { Answer } from '../../reducers/answers/answer';

function start(weekId : number) {
    return {
        type : EDIT_ANSWER_START,
        weekId : weekId
    }
};

function fail(weekId : number) {
    return {
        type : EDIT_ANSWER_ERROR,
        weekId : weekId
    }
};

function success(weekId : number) {
    return {
        type : EDIT_ANSWER_SUCCESS,
        weekId : weekId
    }
};

export default function EditAnswer(weekId : number, answer : Answer) {
    return function(dispatch : Dispatch) {
        dispatch(start(weekId))
        return api.patch(`/week/${weekId}/answer`, answer)
            .then(response => dispatch(success(weekId)))
            .catch(err => dispatch(fail(weekId)));
    }
}