import { Answer } from "./answer";

export const GET_ANSWERS_START = 'get_answers_start';
export const GET_ANSWERS_SUCCESS = 'get_answers_success';
export const GET_ANSWERS_ERROR = 'get_answers_error';

export const ADD_ANSWER_START = 'add_answer_start';
export const ADD_ANSWER_SUCCESS = 'add_answer_success';
export const ADD_ANSWER_ERROR = 'add_answer_error';

export const EDIT_ANSWER_START = 'edit_answer_start';
export const EDIT_ANSWER_SUCCESS = 'edit_answer_success';
export const EDIT_ANSWER_ERROR = 'edit_answer_error';

export interface GetAnswersStartAction {
    type : typeof GET_ANSWERS_START;
    weekId : number;
    userId : number;
}

export interface GetAnswersSuccessAction {
    type : typeof GET_ANSWERS_SUCCESS;
    weekId : number;
    userId : number;
    data : Answer[];
}

export interface GetAnswersErrorAction {
    type : typeof GET_ANSWERS_ERROR;
    weekId : number;
    userId : number;
}

export interface AddAnswerStartAction {
    type : typeof ADD_ANSWER_START;
    weekId : number;
    userId : number;
}

export interface AddAnswerSuccessAction {
    type : typeof ADD_ANSWER_SUCCESS;
    weekId : number;
    userId : number;
}

export interface AddAnswerErrorAction {
    type : typeof ADD_ANSWER_ERROR;
    weekId : number;
    userId : number;
}

export interface EditAnswerStartAction {
    type : typeof EDIT_ANSWER_START;
    weekId : number;
    userId : number;
}

export interface EditAnswerSuccessAction {
    type : typeof EDIT_ANSWER_SUCCESS;
    weekId : number;
    userId : number;
}

export interface EditAnswerErrorAction {
    type : typeof EDIT_ANSWER_ERROR;
    weekId : number;
    userId : number;
}


type GetAnswersAction = GetAnswersStartAction | GetAnswersSuccessAction | GetAnswersErrorAction;
type AddAnswerAction = AddAnswerStartAction | AddAnswerSuccessAction | AddAnswerErrorAction;
type EditAnswerAction = EditAnswerStartAction | EditAnswerSuccessAction | EditAnswerErrorAction;
type AnswersAction = GetAnswersAction | AddAnswerAction | EditAnswerAction;

export default AnswersAction;