import React, { useState, useEffect } from 'react';
import { Grid, Button, Card, CardContent, Select, MenuItem } from '@material-ui/core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import useStyles from './style';
import Page from '../../reducers/pages/page';
import { useSelector, useDispatch } from 'react-redux';
import { selectPages } from '../../reducers/pages';
import { selectAnswers } from '../../reducers/answers';
import AddAnswer from '../../api/answers/add';
import EditAnswer from '../../api/answers/edit';
import GetAnswers from '../../api/answers/get';

export interface AnswerPageProps {
    weekId : number;
    userId : number | undefined; //never displays AnswerPage if undefined but needed to render
}

export default function AnswerPage(props : AnswerPageProps) {
    const classes = useStyles();
    const pageState = useSelector(selectPages);
    const answerState = useSelector(selectAnswers);
    const weekAnswers = answerState[props.weekId];
    const dispatch = useDispatch();
    let startFlushed : number | undefined = undefined;
    let startPitch : number | undefined = undefined;

    if (weekAnswers === undefined || weekAnswers.status === 'unloaded') {
        GetAnswers(props.weekId, props.userId)(dispatch);
    }

    if (weekAnswers !== undefined && props.userId !== undefined && props.userId in weekAnswers.answers) {
        startFlushed = weekAnswers.answers[props.userId].flushedPage;
        startPitch = weekAnswers.answers[props.userId].pitchPage;
    }

    const [flushed, setFlushed] = useState<number | undefined>(startFlushed);
    const [pitch, setPitch] = useState<number | undefined>(startPitch);

    useEffect(() => {
        setFlushed(startFlushed);
        setPitch(startPitch);
    }, [weekAnswers?.status]);

    function setAnswer() {
        if (weekAnswers !== undefined && props.userId! in weekAnswers.answers) {
            EditAnswer(props.weekId, { personId: props.userId!, flushedPage: flushed!, pitchPage: pitch! })(dispatch);
        } else {
            AddAnswer(props.weekId, { personId: props.userId!, flushedPage: flushed!, pitchPage: pitch! })(dispatch);
        }
    }

    return(
        <>
            <Grid container spacing={5}>
                <Grid item xs={6}>
                    <Card className={classes.icon}>
                        <CardContent>
                            <FontAwesomeIcon color='red' size='7x' icon='heart' />
                            <Select
                                onChange={e => setFlushed(e.target.value as number)}
                                value={flushed ?? ''}
                            >
                                {
                                    pageState[props.weekId].pages
                                        .filter(page => page.pageNumber !== pitch)
                                        .map(page =>
                                            <MenuItem 
                                                value={page.pageNumber}
                                            >
                                                {page.description}
                                            </MenuItem>
                                        )
                                }
                            </Select>
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item xs={6}>
                    <Card className={classes.icon}>
                        <CardContent>
                            <FontAwesomeIcon className={classes.icon} color='black' size='7x' icon='spade' />
                            <Select
                                onChange={e => setPitch(e.target.value as number)}
                                value={pitch ?? ''}
                            >
                                {
                                    pageState[props.weekId].pages
                                        .filter(page => page.pageNumber !== flushed)
                                        .map(page =>
                                            <MenuItem 
                                                value={page.pageNumber}
                                            >
                                                {page.description}
                                            </MenuItem>
                                        )
                                }
                            </Select>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
            <Button
                className={classes.button}
                color='primary'
                variant='contained'
                disabled={flushed === undefined || pitch === undefined || (flushed === startFlushed && pitch === startPitch)}
                onClick={setAnswer}
            >
                Submit
            </Button>
            <Button className={classes.button} color='primary' variant='contained' disabled>Reveal</Button>
        </>
    );
}