import api from '../api';
import { Dispatch } from 'redux';
import { GET_ANSWERS_START, GET_ANSWERS_ERROR, GET_ANSWERS_SUCCESS } from '../../reducers/answers/actions';
import { Answer } from '../../reducers/answers/answer';

function start(weekId : number, userId : number | undefined) {
    return {
        type : GET_ANSWERS_START,
        weekId : weekId,
        userId : userId
    }
};

function fail(weekId : number, userId : number | undefined) {
    return {
        type : GET_ANSWERS_ERROR,
        weekId : weekId,
        userId : userId
    }
};

function success(weekId : number, userId : number | undefined, answers : Answer[]) {
    return {
        type : GET_ANSWERS_SUCCESS,
        weekId : weekId,
        userId : userId,
        data : answers
    }
};

interface AnswerResponse {
    person : { id : number },
    flushed : { pageNumber : number },
    pitch : { pageNumber : number }
}

export default function GetAnswers(weekId : number, personId : number | undefined) {
    return function(dispatch : Dispatch) {
        dispatch(start(weekId, personId));
        return api.get(`/week/${weekId}/answer?personId=${personId ?? null}`)
            .then(response => {
                const data = (response.data as AnswerResponse[]).map(a => {
                    return { personId: a.person.id, flushedPage: a.flushed.pageNumber, pitchPage: a.pitch.pageNumber };
                });
                return dispatch(success(weekId, personId, data as Answer[]));
            })
            .catch(err => dispatch(fail(weekId, personId)));
    }
}